export default function RegistSecond({submitModal,confirmModal}) {

  const temp_probrokerRegister=useSelector(data => data.temp_probrokerRegister);
  console.log('>>>temp proborkerREIGSER:',temp_probrokerRegister);
  const login_user = useSelector(data => data.login_user);
  const [companyinfo,setcompanyinfo] = useState({});
  useEffect(async () => {
    let body_info = {
      company_id : login_user.company_id
    }
    let companyinfo = await serverController.connectFetchController('/api/broker/companyinfo_get','POST',JSON.stringify(body_info));

    if(companyinfo){
      console.log('companyinfoss sss:',companyinfo);
      if(companyinfo.success){
        let result=companyinfo.result;

        setcompanyinfo(result);
      } 
      
    }
  },[]);
    return (
        <Container>
          <WrapMember>
            <TopTitle>전문 종목 선택</TopTitle>
            <Wrap>
              <TopInfo>
                <Line>
                  <Left>중개업소등록번호 : </Left>
                  <Right>{companyinfo.realtor_reg_no}</Right>
                </Line>
                <Line>
                  <Left>중개업소명 : </Left>
                  <Right>{companyinfo.company_name}</Right>
                </Line>
                <Line>
                  <Left>중개업소주소 : </Left>
                  <Right>{companyinfo.addr_road}</Right>
                </Line>
                <Line>
                  <Left>대표자명 : </Left>
                  <Right>{companyinfo.ceo_name}</Right>
                </Line>
              </TopInfo>
              <MiddleInfo>
                <SubTitle>전문 종목 <Green>{temp_probrokerRegister.isapart + temp_probrokerRegister.isofficetel + temp_probrokerRegister.isstore + temp_probrokerRegister.isoffice}</Green>건</SubTitle>
                <Box>
                  <Kind>1. 아파트</Kind>
                  <Address>{temp_probrokerRegister.apartaddressroad}({temp_probrokerRegister.apartname})</Address>
                </Box>
                <Box>
                  <Kind>2. 오피스텔</Kind>
                  <Address>{temp_probrokerRegister.officeteladdressroad}({temp_probrokerRegister.officetelname})</Address>
                </Box>
                {
                  temp_probrokerRegister.isstore == 1?
                  <Box>
                    <Kind>3. 상가</Kind>
                  </Box>:
                  null
                }
                {
                  temp_probrokerRegister.isoffice == 1?
                  <Box>
                    <Kind>4. 사무실</Kind>
                  </Box>:
                  null
                }
                
              </MiddleInfo>
              <BottomInfo>

              위와 같이 전문중개업소를 신청합니다.<br/>
              전문중개업소로 선정될 시, 관련 법령을 준수하고 부동산 정보 서비스의 선진화 공정한
              거래질서를 위하여 신의성실의 원칙을 지키겠습니다.

              </BottomInfo>
              <Button>
                  <Submit type="submit" onClick={async ()=>{

                    const formData=new FormData();

                    formData.append('folder','probrokerAuth');
                    formData.append('memid',login_user.memid);
                    formData.append('company_id',login_user.company_id);
                    formData.append('apartaddressroad',temp_probrokerRegister.apartaddressroad);
                    formData.append('apartname',temp_probrokerRegister.apartname);
                    formData.append('isapart',temp_probrokerRegister.isapart);
                    formData.append('isoffice',temp_probrokerRegister.isoffice);
                    formData.append('isofficetel',temp_probrokerRegister.isofficetel);
                    formData.append('isstore',temp_probrokerRegister.isstore);
                    formData.append('officeteladdressroad',temp_probrokerRegister.officeteladdressroad);
                    formData.append('officetelname',temp_probrokerRegister.officetelname);
                    formData.append('companyregfile',temp_probrokerRegister.companyregfile);
                    formData.append('realtorfile',temp_probrokerRegister.realtorfile);
                    console.log('>>>>>form dataaa::',formData);

                    let server_request=await serverController2.connectFetchController('/api/broker/probroker_register','POST',formData);

                    if(server_request){
                      console.log('>>>>server_requestss:',server_request);

                      if(server_request.success){

                      }else{
                        alert(server_request.message);
                      }
                    }
                    submitModal();
                    
                  }}>제출</Submit>
              </Button>
          </Wrap>
      </WrapMember>
  </Container>
  );
}

