import serverController from '../../../server/serverController';

export default function Join() {
  const history=useHistory();
  const login_user=useSelector(data => data.login_user);
  //이용약관
  // const [termservice, setTermService] = useState(false);
  // const openTermService = (onOff) =>{ setTermService(onOff);}

  //개인정보처리방침
  // const [termprivacy, setTermPrivacy] = useState(false);
  // const openTermPrivacy = (onOff) =>{ setTermPrivacy(onOff);}

  //위치기반서비스 이용약관
  // const [termlocation, setTermLocation] = useState(false);
  // const openTermLocation = (onOff) =>{ setTermLocation(onOff);}

  //분양 모달
  // const [bunyang, setBunyang] = useState(false);
  // const openBunyang = (onOff) =>{ setBunyang(onOff);}
  //라이브 시청 모달
  // const [live, setLive] = useState(false);
  //분양 상세이미지 모달
  // const [detailimg, setDetailImg] = useState(false);
  // const [cal, setCal] = useState(false);

  const [modalOption,setModalOption] = useState({show : false,setShow:null,link:"",title:"",submit:{},submitnone:{},cancle:{},confirm:{},confirmgreen:{},confirmgreennone:{},content:{}});


  //여기 두개가 핵심이에여
    //모달 끄는 식
      const offModal = ()=>{
        let option = JSON.parse(JSON.stringify(modalOption));
        option.show = false;
        setModalOption(option);
      }
  
  
      //만약에 필터 모달을 키고 싶으면 아래 함수 호출하시면됩니다.
        const mannerModal = () =>{
          //여기가 모달 키는 거에엽
          setModalOption({
              show:true,
              setShow:offModal,
              title:"중개매너평가",
              content:{type:"components",text:`Testsetsetsetsetestse`,component:<ModalManner/>},
              submit:{show:false , title:"적용" , event : ()=>{offModal(); }},
              cancle:{show:false , title:"초기화" , event : ()=>{offModal(); }},
              confirmgreennone:{show:true , title:"확인" , event : ()=>{offModal(); }}
          });
        }

        const filterModal = () =>{
          //여기가 모달 키는 거에엽
          setModalOption({
              show:true,
              setShow:offModal,
              title:"필터",
              content:{type:"components",text:`Testsetsetsetsetestse`,component:<ModalFilter/>},
              submitnone:{show:true , title:"적용" , event : ()=>{offModal(); }},
              cancle:{show:true , title:"초기화" , event : ()=>{offModal(); }},
              confirmgreennone:{show:false , title:"확인" , event : ()=>{offModal(); }}
          });
        }

        const cancleModal = (prd_identity_id_val,company_id_val,request_memid_val) =>{
          //여기가 모달 키는 거에엽
          setModalOption({
              show:true,
              setShow:offModal,
              title:"중개의뢰 철회",
              content:{type:"text",text:`정말 의뢰를 철회하시겠습니까?`,component:""},
              submit:{show:true , title:"확인" , event : async ()=>{
                offModal();
                //prd_idneity_id어떤매물을,그 매물을 어떤사업체가companyid,그 매물에 대해 업뎃할 상태값(prd_status_val) 넘기기기필요, notifiaciton정보엔 prd_idneitityid,해당 매물을 위임한 사업체id(그걸 이용해 그 소속의 모든 팀원들에게전송), 메시지내용 등.
                let body_info ={
                  prd_identity_id : prd_identity_id_val,//어떤 매물 관련 매물인지
                  company_id : company_id_val,//어떤 수임 전문중개사인지
                  prd_status_val : '의뢰철회',
                  generator_memid : login_user.memid,
                  change_reason: ''
                };
                console.log('의뢰 철회 생성된 매물 의뢰매물으 상태값 의뢰철회상태로 상태값 변경insert추가:',JSON.stringify(body_info));
                let res = await serverController.connectFetchController('/api/broker/brokerRequest_productstatus_updateinsert','POST',JSON.stringify(body_info));
                console.log('res resultsss:',res); //해당 매물관련된 내역 상태변경내역을 추가할뿐이다.

                if(res){
                  if(res.success){
                    alert('의뢰철회 처리되었습니다.');
                    let noti_info = {
                      prd_identity_id : prd_identity_id_val,//어떤 매물 관련 매물에 대한건지
                      company_id : company_id_val,//어떤 수임전문중개사 소속에게 보낼건지
                      message : request_memid_val+'memid 의뢰자  '+prd_identity_id_val+'::txn_id매물 의뢰철회 하였습니다.',
                      noti_type : 4
                    }
                    let noti_res = await serverController.connectFetchController('/api/alram/notification_process','POST',JSON.stringify(noti_info));
                    if(noti_res){
                      if(noti_res.success){
                        console.log('noti_ressss:',noti_res);
                      }
                    }
                    history.push('/Mypage');
                  }else{
                    alert(res.message);
                  }
                }
              }},
              cancle:{show:true , title:"취소" , event : ()=>{offModal(); }},
              confirmgreennone:{show:false , title:"확인" , event : ()=>{
                offModal();
              }}
          });
        }

        const startModal = () =>{
          //여기가 모달 키는 거에엽
          setModalOption({
              show:true,
              setShow:offModal,
              title:"중개 거래개시",
              content:{type:"text",text:`거래를 개시하시겠습니까?`,component:""},
              submit:{show:true , title:"확인" , event : ()=>{offModal(); }},
              cancle:{show:true , title:"취소" , event : ()=>{offModal(); }},
              confirmgreennone:{show:false , title:"확인" , event : (prd_identity_id)=>{
                offModal();
                
                history.push('/Preview/'+prd_identity_id);//미리보기 페이지로 이동!(로그인여부 상관없음) 보안등의 처리 추후에.
              }}
          });
        }

        const cancle2Modal = (prd_identity_id_val,company_id_val,request_memid_val) =>{
          //여기가 모달 키는 거에엽
          setModalOption({
              show:true,
              setShow:offModal,
              title:"위임 취소",
              content:{type:"text",text:`위임 취소하시겠습니까?`,component:""},
              submit:{show:true , title:"확인" , event : async ()=>{
                offModal();
                //개인기업회원이 위임취소요청을 액션발생시에 관련 전문중개사에게 위임취소한다. 관련 내역을 history insert update하면 될뿐이다.
                let body_info = {
                  prd_identity_id : prd_identity_id_val,//어떤 관련 매물인지
                  company_id : company_id_val,//어떤 수임 전문중개사인지
                  prd_status_val : '위임취소',
                  generator_memid : login_user.memid,
                  change_reason : ''
                };
                console.log('위임취소 생성된 매물 의뢰매물으로 상태값 위임취소 상태값 변경insert추가:',JSON.stringify(body_info));
                let res= await serverController.connectFetchController('/api/broker/brokerRequest_productstatus_updateinsert','POST',JSON.stringify(body_info));
                console.log('res resultsss:',res);

                if(res){
                  if(res.success){
                    alert('위임 취소처리되었습니다.');
                    let noti_info = {
                      prd_identity_id : prd_identity_id_val,//어떤 매물 관련 매물에 대한건지
                      company_id : company_id_val,//어떤 수임전문중개사 소속에게 보낼건지
                      message : request_memid_val+'memid 의뢰자 '+prd_identity_id_val+'::txn_id매물 위임취소 하였습니다.',
                      noti_type : 4
                    };
                    let noti_res = await serverController.connectFetchController('/api/alram/notification_process','POST',JSON.stringify(noti_info));
                    if(noti_res){
                      if(noti_res.success){
                        console.log('noti_resss ruseltsss:',noti_res);
                      }
                    }
                    history.push('/Mypage');
                  }else{
                    alert(res.message);
                  }
                }
              }},
              cancle:{show:true , title:"취소" , event : ()=>{offModal(); }},
              confirmgreennone:{show:false , title:"확인" , event : ()=>{offModal(); }}
          });
        }

        const completeModal = () =>{
          //여기가 모달 키는 거에엽
          setModalOption({
              show:true,
              setShow:offModal,
              title:"거래 완료",
              content:{type:"text",text:`거래를 완료하시겠습니까?`,component:""},
              submit:{show:true , title:"확인" , event : ()=>{offModal(); }},
              cancle:{show:true , title:"취소" , event : ()=>{offModal(); }},
              confirmgreennone:{show:false , title:"확인" , event : ()=>{offModal(); }}
          });
        }
        
    return (
        <>
          {/*
            <ImgDetail detailimg={detailimg} setDetailImg={setDetailImg}/>
            <LiveModal live={live} setLive={setLive}/>
            <ModalCalendar cal={cal} setCal={setCal}/>
            <Bunyang bunyang={bunyang} openBunyang={openBunyang} setLive={setLive} setDetailImg={setDetailImg} setCal={setCal}/>
            <MainHeader openBunyang={openBunyang}/>
          */}
          <CommonHeader />
          <Container>
            {
            login_user.user_type=='개인'?
            <SubTitle title='개인' rank={false} cursor={"default"}/>
            :
            <SubTitle title={login_user.company_name} arrow={"▼"} path={"/Team"} rank={true} cursor={"pointer"}/>
            }
              
              <MyRequest mannerModal={mannerModal} cancleModal={cancleModal} filterModal={filterModal} startModal={startModal} cancle2Modal={cancle2Modal} completeModal={completeModal}/>
              <ModalCommon modalOption={modalOption}/>
          </Container>
          <CommonFooter />
          {/*
            <TermService termservice={termservice} openTermService={openTermService}/>
            <TermPrivacy termprivacy={termprivacy} openTermPrivacy={openTermPrivacy}/>
            <TermLocation termlocation={termlocation} openTermLocation={openTermLocation}/>
            <MainFooter openTermService={openTermService} openTermPrivacy={openTermPrivacy} openTermLocation={openTermLocation}/>
          */}
        </>
  );
}

const Container = styled.div`
    width: 100%;
    min-height:calc(100vh - 289px);
    @media ${(props) => props.theme.mobile} {
        min-height:calc(100vh - calc(100vw*(334/428)));
      }
`
