//react
import React ,{useState, useEffect} from 'react';
import {Link,useHistory} from "react-router-dom";


//css
import styled from "styled-components"

//img
import Filter from '../../../../img/member/filter.png';
import Bell from '../../../../img/member/bell.png';
import BellActive from '../../../../img/member/bell_active.png';
import Location from '../../../../img/member/loca.png';
import Set from '../../../../img/member/setting.png';
import Item from '../../../../img/main/item01.png';
import Noimg from '../../../../img/main/main_icon3.png';
import Close from '../../../../img/main/modal_close.png';
import Change from '../../../../img/member/change.png';
import Marker from '../../../../img/member/marker.png';
import ArrowDown from '../../../../img/member/arrow_down.png';

import { Mobile, PC } from "../../../../MediaQuery"

//server process
import serverController from '../../../../server/serverController';

export default function Request({map,setMap,filter,setFilter,reserve,setReserve,value,match_td_info,match_product_info,time_distance,time_status,color,opacity,cond,updateMapModal,updateReserveModal}) {
  const history=useHistory();
  //... 눌렀을때(메뉴)
  const [menu,setMenu] = useState(false);
  const showModal =()=>{  
    setMenu(!menu);
  }


  // 클릭 아이디
  /*const onClickAddress = (data) => {
    console.log(data.reserve_id);
    // setMap(true);
    // updateMapModal();
  }*/

  const onClickAlarm = (e) => {
    // true 알람 활성화, false 알랄 비활성화
    // console.log(e.target.checked);
  }

 
  // 수정 버튼
  const onClickModify = (tr_id,prd_identity_id) => {
    setReserve(true);
    updateReserveModal(tr_id,prd_identity_id);
  }

  if(time_status=='mirae'){
    var message = '+'+time_distance+'일후';
  }else if(time_status =='today'){
    var message = '오늘';
  }else if(time_status =='passed'){
    var message='마감';
  }

  if(cond!='' && value.tr_status==1){
    var message='예약해제';
  }else if(cond!='' && value.tr_status==2){
    var message='예약취소';
  }
  return (
    <Container>
      <Li opacity={opacity}>
        <Img>
          <ItemImg src={match_product_info?match_product_info.prd_img:Noimg} alt="img"/>
          {/*상품이미지가 없을경우*/}
          {/* <ItemImg src={Noimg} alt="img"/> */}
        </Img>

        <Infos>
          <Condition>상태:{message}<Orange color={color}>({value.tour_reservDate})</Orange></Condition>
          <Number>등록번호 {match_product_info.prd_identity_id} {value.tr_name}({value.tr_phone})</Number>
          <Address>
            <div className="cursor-p" onClick={() => {updateMapModal(match_product_info);}}>
              <AddressTitle>{match_product_info.addressroad}<LocaImg src={match_product_info.prd_img}/></AddressTitle>
            </div>
          </Address>
          <DateTime>
            <Date>{value.tour_reservDate}</Date>
            <Time>{match_td_info.td_text}({match_td_info.td_starttime}~{match_td_info.td_endtime})</Time>
          </DateTime>
        </Infos>
        <RightMenu>
          <Alarm>
            <AlarmCheck type="checkbox" name="" value="" onClick={(e) => onClickAlarm(e)} id={"check"+value.tr_id}/>
            <Label for={"check"+value.tr_id}/>
          </Alarm>
          <Menu>
            <div className="cursor-p" onClick={showModal}>
              <MenuIcon/>
                {
                  menu ?
                    value.tr_status==0?
                    <InMenu>
                      <Div>
                        <InDiv className="cursor-p" onClick={ async() => {
                          if(window.confirm('취소하시겠습니까??')){
                            let body_info = {
                              tr_id_val : value.tr_id//취소처리할.
                            }
                            let res = await serverController.connectFetchController('/api/broker/mybrokerproduct_tourReservcancle','POST',JSON.stringify(body_info));
                            if(res){
                              if(res.success){
                                let noti_info = {
                                  prd_identity_id : match_product_info.prd_identity_id,//취소처리할 관련매물관련 접수
                                  //request_memid : value.mem_id,
                                  message: value.tr_name+'('+value.tr_phone+')'+value.tr_email+' 님이'+match_product_info.prd_identity_id+'매물에 대한 신청하신 물건투어예약접수 취소하였습니다.',
                                  company_id: match_product_info.company_id,//그걸 수임담당하고있는 전문중개사 소소게에게 보낸다.
                                  noti_type : 9
                                }
                                let noti_res = await serverController.connectFetchController('/api/alram/notification_process','POST',JSON.stringify(noti_info));
                                if(noti_res){
                                  if(noti_res.success){
                                    alert('예약 취소되었습니다.');

                                    history.push('/Mypage');
                                  }else{
                                    alert(noti_res.message);
                                  }
                                }
                              }else{
                                alert(res.message);
                              }
                            }
                          }
                           
                        }}>예약취소</InDiv>
                      </Div>
                      <Div>
                        <InDiv className="cursor-p" onClick={()=>{onClickModify(value.tr_id,match_product_info.prd_identity_id)}}>수정</InDiv>
                      </Div>
                      <Div>
                        <InDiv className="cursor-p" onClick={ async() => {
                          if(window.confirm('삭제하시겠습니까??')){
                            let delete_info={
                              tr_id:value.tr_id//삭제할 trid값 그냥 삭제한다.
                            }
                            let delete_process=await serverController.connectFetchController('/api/broker/mybrokerProduct_tourRservdelete','POST',JSON.stringify(delete_info));
 
                            if(delete_process){
                              console.log('삭제 결과:',delete_process);
                            }else{
                              alert(delete_process.message);
                            }
                          }
                          
                        }}>삭제</InDiv>
                      </Div>
                    </InMenu>
                    :
                    <InMenu>
                      <Div>
                        <InDiv className="cursor-p" onClick={ async() => {
                          if(window.confirm("삭제하시겠습니까??")){
                            let delete_info={
                              tr_id:value.tr_id//삭제할 trid값 그냥 삭제한다.
                            }
                            let delete_process=await serverController.connectFetchController('/api/broker/mybrokerProduct_tourRservdelete','POST',JSON.stringify(delete_info));
 
                            if(delete_process){
                              console.log('삭제 결과:',delete_process);

                              if(delete_process.success){
                                alert('삭제되었습니다.');
                                history.push('/Mypage');
                              }else{
                                alert(delete_process.message);
                              }
                            }
                          }
                           
                        }}>삭제</InDiv>
                      </Div>
                    </InMenu>
                  :
                  null
                }
            </div>
          </Menu>
        </RightMenu>
      </Li>
    </Container>
  );
}

const Pb = styled.b`
  display:block;
  @media ${(props) => props.theme.mobile} {
        display:inline;
    }
`
