  const [except_datelist,setExcept_datelist] = useState([]);//표현에서 제외할 특정날짜 리스트
  const [result_usedatalist,setResult_usedatalist] = useState([]);//사용할 표현할 최종데이터리스트 초기값 배열

    //여기 두개가 핵심이에여
