//react redux
import {useSelector} from 'react-redux';
import {Login_userActions} from '../../../store/actionCreators';

export default function SubTitle() {
  const login_user = useSelector(data => data.login_user);
  //분양사회원인 경우 로그인한 회원memid에게 부여된 분양프로젝트들리스트가 띄워진다. 중개사나 기업인경우는 그냥 소속id ocmpanyname하나가 뜨면된다.

  if(login_user.user_type != '분양대행사'){
    var sosokList = [
      {
        Team_id : 0,
        path : "/Mypage",
        src : NoImg,
        title : login_user.company_name,//소속 업체(기업명,중개사명)로 리덕스 로그인유저 companyname업체명(소속업체 소속중개사,기업체명으로 소속명 변경)
        src2 : RightArrow
      },
     
    ]
  }
  const TeamListItem =[
    {
      Team_id : 0,
      path:"/Mypage",
      src:NoImg,
      title:"삼성물산",
      src2:RightArrow
    },
    {
      /*중개사일경우*/
      Team_id : 1,
      path:"/Mypage",
      src:NoImg,
      title:"럭키공인중개사",
      src2:RightArrow
    },
    {
      /*분양대행사 일경우*/
      Team_id : 2,
      path:"/Mypage",
      src:NoImg,
      title:"AAA 프로젝트",
      src2:RightArrow
    }
    ]


    const onClickTeam = (value) => {
      // 클릭 id
      // console.log(value.Team_id);
      console.log('클릭 teamid및 소속업체명:',value.title,value.Team_id);

      Login_userActions.companynamechange({company_name : value.title});
    }

    return (
        <Container>
          <WrapTeamBody>
            <TeamList>
            {
              sosokList.map((value) => {
                return(
                  <List onClick={() => onClickTeam(value)}>
                    <Link to={value.path} className="data_link"></Link>
                      <TeamTitle>
                        <ItemImg src={value.src}/>
                        <Title>{value.title}</Title>
                      </TeamTitle>
                      <GoDetail>
                        <RightArrowImg src={value.src2} />
                      </GoDetail>
                  </List>
                )
              }
            )
          }
            </TeamList>
          </WrapTeamBody>
        </Container>
  );
}

