//react
import React ,{useState, useEffect} from 'react';
import {Link} from "react-router-dom";

import styled from "styled-components"

//component
import MainHeader from '../../../component/common/MainHeader';
import SubTitle from '../../../component/common/SubTitle';
import MyLive from '../../../component/member/mypage/mylive/MyLive';
import MainFooter from '../../../component/common/MainFooter';
import TermService from '../../../component/common/TermsOfService';
import TermPrivacy from '../../../component/common/TermsOfPrivacy';
import TermLocation from '../../../component/common/TermsOfLocation';
import Bunyang from '../../../component/common/bunyang/Bunyang';
import ImgDetail from "../../../component/common/bunyang/ImgDetail";
import LiveModal from "../../../component/common/bunyang/LiveModal";
import ModalCalendar from "../../../component/common/bunyang/ModalCalendar";
import ModalCommon from '../../../component/common/modal/ModalCommon';
import ModalFilterComponent from '../../../component/member/mypage/mylive/ModalFilterComponent';

import CommonHeader from '../../../component/common/commonHeader';
import CommonFooter from '../../../component/common/commonFooter';
import serverController from '../../../server/serverController';
import { useSelector } from 'react-redux';

export default function MyLivePage() {

  const [filter,setFilter] = useState({
    order: "DESC",
    sort: "ALL",
    search :"",
    index:0
  });
  const [list,setList] = useState([]);
  
  const userInfo = useSelector(e => e.login_user);

  useEffect( () => {
    updateList();
  }, [])


  async function updateList(){
    let result = await serverController.connectFetchController(
      `/api/bunyang/my/reservation?mem_id=${userInfo.memid}&tr_type=${2}&order=${filter.order}&sort=${filter.sort}`
      ,"GET"
      ,null
    );
    setList(result.data);
  }

  function clearFilter(){
    setFilter({  order: "DESC",  sort: "ALL", search :"",index:0 });
  }

  //물건예약수정 모달창
  const [reserve,setReserve] = useState(false);
  const [modalOption,setModalOption] = useState({show : false,setShow:null,link:"",title:"",submitnone:{},cancle:{},confirm:{},confirmgreen:{},content:{}});
  const [sort, setSort] = useState(0); // 정렬 기준
  const [condi, setCondi] = useState(0); // 상태

  //여기 두개가 핵심이에여 넵!
  //모달 끄는 식
  const offModal = ()=>{
    let option = JSON.parse(JSON.stringify(modalOption));
    option.show = false;
    setModalOption(option);
  }
  // 초기화 버튼 클릭
  const onClickReset = () => {
    offModal();
    setSort(0);
    setCondi(0);
  }
  
  //만약에 필터 모달을 키고 싶으면 아래 함수 호출하시면됩니다.
  const updateModal = () =>{
    //여기가 모달 키는 거에엽
    setModalOption({
        show:true,
        setShow:offModal,
        title:"필터",
        content:{type:"components",text:`Testsetsetsetsetestse`,component:<ModalFilterComponent filter={filter} setFilter={setFilter}/>},
        submitnone:{show:true , title:"적용" , event : ()=>{offModal(); updateList(); }},
        cancle:{show:true , title:"초기화" , event : ()=>{ onClickReset(); }},
        confirm:{show:false , title:"확인" , event : ()=>{offModal(); }}
    });
  }

  return (
    <>
      {/*
        <ImgDetail detailimg={detailimg} setDetailImg={setDetailImg}/>
        <LiveModal live={live} setLive={setLive}/>
        <ModalCalendar cal={cal} setCal={setCal}/>
        <Bunyang bunyang={bunyang} openBunyang={openBunyang} setLive={setLive} setDetailImg={setDetailImg} setCal={setCal}/>
        <MainHeader openBunyang={openBunyang}/>
      */}
      <CommonHeader />
      <Container>
          <SubTitle title={"소속명"} arrow={"　▼"} path={"/Team"} cursor={"pointer"}/>
          <ModalCommon modalOption={modalOption}/>
          <MyLive list={list} setList={setList} updateModal={updateModal} updateList={updateList}/>
      </Container>
      <CommonFooter />
      {/*
        <TermService termservice={termservice} openTermService={openTermService}/>
        <TermPrivacy termprivacy={termprivacy} openTermPrivacy={openTermPrivacy}/>
        <TermLocation termlocation={termlocation} openTermLocation={openTermLocation}/>
        <MainFooter openTermService={openTermService} openTermPrivacy={openTermPrivacy} openTermLocation={openTermLocation}/>
      */}
    </>
  );
}

const Container = styled.div`
    width: 100%;
    min-height:calc(100vh - 289px);
    @media ${(props) => props.theme.mobile} {
        min-height:calc(100vh - calc(100vw*(334/428)));
      }
`
