//react
import React ,{useState, useEffect} from 'react';

//style
import styled from "styled-components";

//components
import ModalCalendarFirst from "./ModalCalendarFirst";
import ModalCalendarSecond from "./ModalCalendarSecond";
import ModalCalendarThird from "./ModalCalendarThird";

import serverController from '../../../server/serverController'
import ModalCommon from '../modal/ModalCommon';
import { useSelector } from 'react-redux';

function checkZero(checkString){
  return checkString.toString().length == 1 ?  "0" + checkString : checkString;
}

function getDateType(date){
  //date.setDate(date.getDate() + 1);
  var temp = `${checkZero(date.getFullYear())}/${checkZero(date.getMonth() + 1)}/${checkZero(date.getDate())}`;
  return temp;
}


function getDateTimeType(date){
  //date.setDate(date.getDate() + 1);
  var temp = `${checkZero(date.getHours())}:${checkZero(date.getMinutes())}:00`;
  return temp;
}




export default function ModalCalendar({bunyangDetail,cal, setCal}){
  const [value, onChange] = useState(new Date());
  const [pageIndex , setPageIndex] = useState(0);
  const [tour,setTour] = useState({});
  const userInfo = useSelector(e => e.login_user);

  

  const [SelectDate, setSelectDate] = useState(new Date());
  const [selectTime, setSelectTime] = useState(new Date());

  const [checkedTime, setcheckedTime] = useState('');

  const [userList, setUserList] = useState([]);
  const [modalOption,setModalOption] = useState({show : false,setShow:null,link:"",title:"",submit:{},cancle:{},confirm:{},confirmgreennone:{},content:{}});

  useEffect(() => {  
    
    if(!bunyangDetail.bp_id)
      return;

    serverController.connectFetchController(`/api/bunyang/reservation/setting?bp_id=${bunyangDetail.bp_id}&tour_type=3`,'GET',null,function(res){
      if(res.success == 1){
        setTour(res.data[0]);
      }
    });
  }, [bunyangDetail])


  
  //여기 두개가 핵심이에여
  //모달 끄는 식
  const offModal = ()=>{
    let option = JSON.parse(JSON.stringify(modalOption));
    option.show = false;
    setModalOption(option);
  }

  const finalModal= () =>{
    setModalOption({
      show:true,
      setShow:offModal,
      title:"등록",
      content:{type:"text",text:`이름과 이메일을 확인해주세요.`,component:""},
      submit:{show:false , title:"" , event : ()=>{offModal(); }},
      cancle:{show:false , title:"" , event : ()=>{offModal(); }},
      confirm:{show:false , title:"확인" , event : ()=>{offModal();}},
      confirmgreennone:{show:true , title:"확인" , event : ()=>{offModal();}}
    });
  }

  const successInsertReservation = () =>{
    setModalOption({
      show:true,
      setShow:offModal,
      title:"등록",
      content:{type:"text",text:`등록되었습니다.`,component:""},
      submit:{show:false , title:"" , event : ()=>{offModal(); }},
      cancle:{show:false , title:"" , event : ()=>{offModal(); }},
      confirm:{show:false , title:"확인" , event : ()=>{offModal();}},
      confirmgreennone:{show:true , title:"확인" , event : ()=>{offModal();setCal(false);updatePageIndex(0)}}
    });
  }

  const failInsertReservation = () =>{
    setModalOption({
      show:true,
      setShow:offModal,
      title:"등록",
      content:{type:"text",text:`등록되었습니다.`,component:""},
      submit:{show:false , title:"" , event : ()=>{offModal(); }},
      cancle:{show:false , title:"" , event : ()=>{offModal(); }},
      confirm:{show:false , title:"확인" , event : ()=>{offModal();}},
      confirmgreennone:{show:true , title:"확인" , event : ()=>{offModal();setCal(false);updatePageIndex(0)}}
    });
  }
  
  const failModal = () =>{
    setModalOption({
      show:true,
      setShow:offModal,
      title:"등록",
      content:{type:"text",text:`방문 예약이 불가능한 분양입니다.`},
      submit:{show:false , title:"적용" , event : ()=>{offModal(); }},
      cancle:{show:false , title:"초기화" , event : ()=>{offModal(); }},
      confirm:{show:false , title:"확인" , event : ()=>{offModal(); }},
      confirmgreennone:{show:true , title:"확인" , event : ()=>{offModal(); setCal(false); updatePageIndex(0);}}
  });
  }
  const pageLoader = () =>{
    switch (pageIndex) {
      case 0: return <ModalCalendarFirst tour={tour} updatePageIndex={updatePageIndex} offModal={offModal} setModalOption={setModalOption} cal={cal} setCal={setCal} SelectDate={SelectDate} setSelectDate={setSelectDate}/>;
      case 1: return <ModalCalendarSecond tour={tour} selectTime={selectTime} updatePageIndex={updatePageIndex} cal={cal} setCal={setCal} SelectDate={SelectDate} setSelectTime={setSelectTime} checkedTime={checkedTime} setcheckedTime={setcheckedTime}/>
      case 2: return <ModalCalendarThird tour={tour} userList={userList} setUserList={setUserList} offModal={offModal} setModalOption={setModalOption} finalModal={finalModal} confimReservation={confimReservation} updatePageIndex={updatePageIndex} cal={cal} setCal={setCal} />
      default: return <ModalCalendarFirst tour={tour} updatePageIndex={updatePageIndex} cal={cal} setCal={setCal} SelectDate={SelectDate} setSelectDate={setSelectDate}/>;
    }
  }


  let blockEvent = false;
  const confimReservation = () =>{
    if(!tour){
      failModal();
      return;
    }


    if(blockEvent){
      return;
    }
    blockEvent = true;

    let data = {
      tour_id : tour.tour_id,
      td_id : tour.td_id,
      userList : userList,
      tr_name : userInfo.user_name ? userInfo.user_name : "프래프이름",
      tr_email : userInfo.email ? userInfo.email : "aid@prefinc.com",
      tr_phone : userInfo.phone ? userInfo.phone :"01000001234",
      mem_id : userInfo.memid ? userInfo.memid : 43,
      tr_type : 1,
      reserv_start_time : getDateType(SelectDate) + " " + getDateTimeType(selectTime),
      bp_id:bunyangDetail.bp_id,
      tr_group_id : new Date().getTime() + "" + tour.tour_id + userInfo.memid
    }

    serverController.connectFetchController(`/api/bunyang/reservation`,'POST',JSON.stringify(data),function(res){
      if(res.success == 1){
        successInsertReservation();
        blockEvent = false;
      }
      else{
        failInsertReservation();
        blockEvent = false;
      }
    });
  }



  const updatePageIndex = (index) =>{
    if(index == 0)
      setPageIndex(0);
    else if(index == 1)
      setPageIndex(1);
    else
      setPageIndex(index);
  }

  if(cal == false)
    return null;
    return (
      <Container>
        <ModalBg onClick={()=>{setCal(false)}}/>
        {
          pageLoader()
        }
        <ModalCommon modalOption={modalOption} />
      </Container>
    );
}

const Container = styled.div`
  width:100%;

`
const ModalBg = styled.div`
  position:fixed;
  width:100%;height:100%;left:0;top:0;
  display:block;content:'';background:rgba(0,0,0,0.05);
  z-index:1001;
`
