import React ,{useState, useEffect} from 'react';
import DaumPostcode from 'react-daum-postcode';

export default function SearchStoreOfficeApi({setapart_addressroad,setaptname,setAddressApi}) {

  const handleComplete = (data) => {
    console.log('==>>dataresultss serach data results:',data);
    let fullAddress = data.address;
    let extraAddress = '';

    if (data.addressType === 'R') {
      if (data.bname !== '') {
        extraAddress += data.bname;
      }
      if (data.buildingName !== '') {
        extraAddress += (extraAddress !== '' ? `, ${data.buildingName}` : data.buildingName);
      }
      fullAddress += (extraAddress !== '' ? ` (${extraAddress})` : '');
    }

    console.log(fullAddress);  // e.g. '서울 성동구 왕십리로2길 20 (성수동1가)'

    setapart_addressroad(data.roadAddress);
    setaptname(data.buildingName);//빌딩이름 건물이름(아파트 케이스의경우)

    setAddressApi(false);
  }

    return (
        <div>
          <DaumPostcode
            onComplete={handleComplete}
          />
        </div>
  );
}
